.search-bar {
  padding: 10px;
  width: 380px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0px 2px 8px #bfbfbf;
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 100;
}

.search-bar__input {
  padding: 7px 10px 6px;
  width: 100%;
  border: 1px solid #dedede;
  border-radius: 4px;
}

.search-bar__input:active,
.search-bar__input:focus {
  outline: none;
  border-color: #3589eb;
}

.search-bar__input::-webkit-input-placeholder {
  color: #000;
}
.search-bar__input:-ms-input-placeholder {
  color: #000;
}
.search-bar__input::-moz-placeholder {
  opacity: 1;
  color: #000;
}
.search-bar__input:-moz-placeholder {
  opacity: 1;
  color: #000;
}

.places {
  margin: 0;
  padding: 2px 10px 10px;
  width: 380px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0px 2px 8px #bfbfbf;
  position: fixed;
  top: 92px;
  bottom: 20px;
  left: 20px;
  z-index: 100;
}

.places__item {
  padding-top: 8px;
  padding-bottom: 10px;
  padding-right: 25px;
  border-top: 1px solid #dedede;
  list-style: none;
  position: relative;
  cursor: move;
}

.places__item:first-child {
  border-top: none;
}

.places__item-remove {
  width: 35px;
  height: 25px;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 6px;
  right: -10px;
  cursor: pointer;
}

.places__item-remove:hover,
.places__item-remove:active,
.places__item-remove:focus {
  color: #e02424;
  outline: none;
}
* {
  box-sizing: border-box;
  font-size: 1rem;
}

html {
  height: 100%;
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

